import React, { useState } from 'react';
import './CryptoForm.css';
import { getCryptoPriceBySymbol } from '../services/cryptoApi';

const CryptoForm = ({ onAddCrypto }) => {
  const [formData, setFormData] = useState({
    symbol: '',
    quantity: '',
    price: ''
  });
  
  const [isLoadingPrice, setIsLoadingPrice] = useState(false);
  const [priceError, setPriceError] = useState('');
  const [lastSuccessfulSymbol, setLastSuccessfulSymbol] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    
    // 
    if (e.target.name === 'price' || e.target.name === 'symbol') {
      setPriceError('');
    }
  };

  const resetErrorState = () => {
    setPriceError('');
    setIsLoadingPrice(false);
  };

  const fetchPrice = async () => {
    const symbol = formData.symbol.trim();
    
    if (!symbol) {
      setPriceError('');
      return;
    }

    // 
    resetErrorState();
    setIsLoadingPrice(true);

    try {
      console.log(`: ${symbol}`);
      const priceData = await getCryptoPriceBySymbol(symbol);
      
      console.log(':', priceData);
      setFormData(prev => ({
        ...prev,
        price: priceData.price.toString()
      }));
      
      setLastSuccessfulSymbol(symbol.toUpperCase());
      
      // 
      setPriceError(` ${priceData.symbol}: ${priceData.price.toLocaleString()}`);
      setTimeout(() => {
        setPriceError('');
      }, 3000);
      
    } catch (error) {
      console.error(':', error);
      
      // 
      let errorMessage = error.message;
      
      // 
      if (errorMessage.includes('')) {
        errorMessage += ` BTCETHADA`;
      } else if (errorMessage.includes('')) {
        errorMessage += ` `;
      }
      
      setPriceError(errorMessage);
    } finally {
      setIsLoadingPrice(false);
    }
  };

  const retryFetchPrice = async () => {
    console.log('...');
    await fetchPrice();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (formData.symbol && formData.quantity && formData.price) {
      onAddCrypto({
        symbol: formData.symbol.toUpperCase(),
        quantity: parseFloat(formData.quantity),
        price: parseFloat(formData.price)
      });
      setFormData({ symbol: '', quantity: '', price: '' });
      setPriceError('');
      setLastSuccessfulSymbol('');
    }
  };

  const isSuccessMessage = priceError.includes('');
  const isNetworkError = priceError.includes('') || priceError.includes('');

  return (
    <div className="crypto-form">
      <h3 className="form-title">
        <span className="emoji"></span>
        
      </h3>

      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="symbol">
            <span className="emoji"></span>
            
            {lastSuccessfulSymbol && (
              <span className="last-success">
                : {lastSuccessfulSymbol}
              </span>
            )}
          </label>
          <input
            type="text"
            id="symbol"
            name="symbol"
            value={formData.symbol}
            onChange={handleChange}
            placeholder="BTC, ETH, ADA, DOT..."
            className="form-input"
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="quantity">
            <span className="emoji"></span>
            
          </label>
          <input
            type="number"
            id="quantity"
            name="quantity"
            value={formData.quantity}
            onChange={handleChange}
            placeholder="0.001"
            step="any"
            className="form-input"
            required
          />
        </div>

        <div className="form-group">
          <label htmlFor="price">
            <span className="emoji"></span>
             ()
          </label>
          <div className="price-input-container">
            <div className="price-input-wrapper">
              <input
                type="number"
                id="price"
                name="price"
                value={formData.price}
                onChange={handleChange}
                placeholder="  "
                step="any"
                className="form-input"
                required
              />
            </div>
            <button
              type="button"
              onClick={fetchPrice}
              disabled={!formData.symbol.trim() || isLoadingPrice}
              className="fetch-price-btn"
            >
              {isLoadingPrice ? (
                <>
                  <span className="spinner"></span>
                  ...
                </>
              ) : (
                <>
                  <span className="emoji"></span>
                  
                </>
              )}
            </button>
          </div>
          
          {priceError && (
            <div className={`error-message ${isSuccessMessage ? 'success' : 'error'}`}>
              <span className="emoji">{isSuccessMessage ? '' : ''}</span>
              {priceError}
              {isNetworkError && !isLoadingPrice && (
                <button
                  type="button"
                  onClick={retryFetchPrice}
                  className="retry-btn"
                >
                  <span className="emoji"></span>
                  
                </button>
              )}
            </div>
          )}
        </div>

        <button type="submit" className="submit-btn">
          <span className="emoji"></span>
          
        </button>
      </form>
    </div>
  );
};

export default CryptoForm; 